<template>
  <div class="list-students">
    <search @searchList="searchList" />
    <list-info
      ref="listInfo"
      @showAddAdmin="addAdminShow = true"
      @searchList="searchList"
    />
    <tip
      v-if="addAdminShow"
      v-model="addAdminShow"
      btn-text="确定"
      :has-footer="false"
    >
      <section slot="content" class="center">
        <el-form
          ref="ruleForm"
          :model="ruleAdmin"
          :rules="rules"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="选择学员" prop="admin">
            <el-select
              v-model="ruleAdmin.admin"
              filterable
              placeholder="请选择"
            >
              <el-option
                v-for="(item, index) of userList"
                :key="index"
                :label="item.name"
                :value="item._id"
              />
            </el-select>
          </el-form-item>
          <el-form-item class="sure">
            <el-button type="primary" @click="submitForm('ruleForm')"
              >确定</el-button
            >
          </el-form-item>
        </el-form>
      </section>
    </tip>
  </div>
</template>

<script>
import Search from './components/Search'
import ListInfo from './components/ListInfo'
import Tip from '@/components/Dialog'
import { userAll, addStudent } from '@/api/learningAdmin'
export default {
  components: {
    Search,
    ListInfo,
    Tip
  },
  data() {
    return {
      addAdminShow: false,
      userList: [],
      ruleAdmin: {
        admin: ''
      },
      rules: {
        admin: [{ required: true, message: '请选择管理员', trigger: 'change' }]
      }
    }
  },
  watch: {
    addAdminShow() {
      if (!this.addAdminShow) this.ruleAdmin.admin = ''
    }
  },
  async mounted() {
    const { data } = await userAll()
    if (data && data.length) this.userList = data
  },
  methods: {
    btnAddAdminShow() {
      this.addAdminShow = false
    },
    submitForm(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          const { data } = await addStudent({
            admin: this.ruleAdmin.admin
          })
          if (!data.result) {
            return this.$message({ message: data.message, type: 'error' })
          }
          await this.$refs['listInfo'].getList()
          this.addAdminShow = false
        } else {
          return false
        }
      })
    },
    searchList(info) {
      this.$refs['listInfo'].getList(info)
    }
  }
}
</script>

<style lang="scss" scoped>
.list-students {
  .sure {
    /deep/.el-form-item__content {
      margin-left: 0 !important;
    }
  }
}
</style>
