<template>
  <div class="students-list-info">
    <div class="title">
      <div>学员列表</div>
      <el-button type="primary" @click="showAdd">选择学员</el-button>
    </div>
    <el-table :data="list" style="width: 100%" :empty-text="hasListInfo">
      <el-table-column
        fixed
        prop="num"
        label="序号"
        min-width="100"
        align="center"
      />
      <el-table-column
        prop="id"
        label="钉钉ID"
        min-width="160"
        align="center"
      />
      <el-table-column
        prop="name"
        label="学员姓名"
        min-width="120"
        align="center"
      />
      <el-table-column
        prop="first_login_time"
        label="首次登录时间"
        min-width="120"
        align="center"
      />
      <el-table-column
        prop="avatar"
        label="头像"
        min-width="120"
        align="center"
      >
        <template slot-scope="scope">
          <el-avatar :size="30" :src="scope.row.avatar" />
        </template>
      </el-table-column>
      <el-table-column
        prop="stage"
        label="当前阶段"
        min-width="120"
        align="center"
      />
      <el-table-column
        prop="stage_status"
        label="阶段状态"
        min-width="120"
        align="center"
      />
      <el-table-column
        prop="plan_name"
        label="使用计划"
        min-width="120"
        align="center"
      />
      <el-table-column fixed="right" label="操作" width="150" align="center">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="handleClick(scope.row)"
            >查看</el-button
          >
          <el-button
            type="text"
            size="small"
            @click="
              delShow = true
              rowInfo = scope.row
            "
            >删除</el-button
          >
          <el-button type="text" size="small" @click="assessment(scope.row)"
            >去考核</el-button
          >
          <span
            v-if="
              scope.row.stage_status === '待考试' && !scope.row.is_commented
            "
            class="tip-spot"
          />
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        :current-page="currentPage"
        :page-sizes="[12, 18, 24]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
    <el-dialog
      title="删除提示"
      :visible.sync="delShow"
      width="30%"
      center
      top="30vh"
    >
      <p class="center">删除后，该学员信息将不再保留，请确定是否删除？</p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delShow = false">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import '../../common.scss'
import { studentsList, delStudent } from '@/api/learningAdmin'
export default {
  data() {
    return {
      list: [],
      currentPage: 1,
      delShow: false,
      pageSize: 12,
      count: 0,
      rowInfo: '',
      hasListInfo: '暂无数据',
      workShiftShow: false // 轮岗提示
    }
  },
  computed: {
    getStageStatus() {
      return function(stage_status) {
        if (stage_status === 'doing') return '正在进行'
        if (stage_status === 'exam') return '待考试'
        if (stage_status === 'eliminate') return '淘汰'
        if (stage_status === 'delay') return '延期'
        if (stage_status === 'pass') return '通过'
      }
    },
    getStage() {
      return function(stage, job_rotation_position) {
        if (stage === job_rotation_position) return '轮岗阶段'
        else {
          if (stage === -1) return '试岗阶段'
          if (stage < job_rotation_position) {
            if (stage === 0) return '第一阶段'
            if (stage === 1) return '第二阶段'
            if (stage === 2) return '第三阶段'
            if (stage === 3) return '第四阶段'
            if (stage === 4) return '第五阶段'
            if (stage === 5) return '第六阶段'
          }
          if (stage > job_rotation_position) {
            stage = stage - job_rotation_position
            if (stage === 0) return '第二阶段'
            if (stage === 1) return '第三阶段'
            if (stage === 2) return '第四阶段'
            if (stage === 3) return '第五阶段'
            if (stage === 4) return '第六阶段'
            if (stage === 5) return '第七阶段'
          }
        }
      }
    }
  },
  created() {
    if (this.$route.query.type) {
      this.getList({ stage: this.$route.query.type, stage_status: 'exam' })
    } else this.getList()
  },
  methods: {
    handleClick(row) {
      this.$router.push(
        `/learning-admin/studentsList/students-detail/${row.admin_id}`
      )
    },
    assessment(row) {
      this.$router.push(
        `/learning-admin/studentsList/ph-ex-result/${row.admin_id}`
      )
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getList()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getList()
    },
    async confirm() {
      const { data } = await delStudent(this.rowInfo.user_id)
      if (data && data.result) {
        this.$message({
          message: '删除成功',
          type: 'success'
        })
      }
      await this.$emit('searchList')
      this.delShow = false
    },
    showAdd() {
      this.$emit('showAddAdmin')
    },
    async getList(search) {
      let info
      if (search) {
        this.hasListInfo = '暂无数据，请重新输入关键词查询'
        info = {
          pageSize: this.pageSize,
          name: search.name || undefined,
          started_at: search.time ? search.time[0] : '',
          ended_at: search.time ? search.time[1] : '',
          user_id: search.user_id || undefined,
          stage_status: search.stage_status || undefined,
          plan_name: search.plan_name || undefined,
          page: this.currentPage,
          stage: search.stage || undefined
        }
      } else {
        this.hasListInfo = '暂无数据'
        info = {
          pageSize: this.pageSize,
          page: this.currentPage
        }
      }
      const { data } = await studentsList(info)
      if (data) {
        if (data.list.length) {
          this.list = data.list.map((item, index) => {
            return {
              num: index + 1,
              id: item.user_id,
              name: item.name,
              first_login_time: item.first_login_time.split('T')[0],
              avatar: item.admin ? item.admin.avatar : '',
              stage: this.getStage(
                item.stage,
                item.plan ? item.plan.job_rotation_position : ''
              ),
              stage_status: this.getStageStatus(item.stage_status),
              user_id: item._id,
              admin_id: item.admin ? item.admin._id : '',
              plan_name: item.plan_name || '/',
              is_commented: item.is_commented
            }
          })
          this.workShiftShow = this.list.some(item => {
            return item.stage_status === 'exam'
          })
          this.count = data.count
        } else this.list = data.list
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.students-list-info {
  margin: 20px 0;
  padding: 20px;
  border: 1px solid #ebeef5;
  .title {
    display: flex;
    justify-content: space-between;
    line-height: 32px;
    /deep/button {
      padding: 6px 20px;
      height: 32px;
    }
  }
  /deep/.el-dialog__wrapper {
    button {
      padding: 6px 20px;
      height: 32px;
      &.el-button--text {
        padding: 0;
      }
    }
    .el-dialog__headerbtn {
      top: 10px;
      right: 5px;
    }
  }
  .pagination {
    margin-top: 50px;
    text-align: center;
  }
  /deep/.cell {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .tip-spot {
    position: relative;
    top: -5px;
    display: inline-block;
    width: 3px;
    height: 3px;
    border: 2px solid #f00;
    border-radius: 50%;
  }
}
</style>
