<template>
  <div class="search">
    <el-form
      label-position="left"
      label-width="100px"
      :model="condition"
      inline
    >
      <el-form-item label="学员名称" class="search-item">
        <el-input v-model="condition.name" clearable placeholder="请输入姓名" />
      </el-form-item>
      <template v-if="isOpen">
        <el-form-item label="首次登录时间" class="search-item">
          <el-date-picker
            v-model="condition.time"
            type="daterange"
            align="right"
            unlink-panels
            range-separator="-"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="pickerOptions"
            value-format="yyyy-MM-dd"
          />
        </el-form-item>
        <el-form-item label="当前阶段" class="search-item">
          <el-select v-model="condition.stage" clearable placeholder="请选择">
            <el-option
              v-for="item in stageList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="阶段状态" class="search-item">
          <el-select
            v-model="condition.stage_status"
            clearable
            placeholder="请选择"
          >
            <el-option
              v-for="item in stateList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="钉钉ID" class="search-item">
          <el-input
            v-model="condition.user_id"
            clearable
            placeholder="请输入钉钉ID"
          />
        </el-form-item>
      </template>
      <el-form-item label="计划名称" class="search-item">
        <el-input
          v-model="condition.plan_name"
          clearable
          placeholder="请输入计划名称"
        />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" class="select-btn" @click="onSubmit"
          >查询</el-button
        >
      </el-form-item>
      <el-button type="text" class="arrow" @click="isOpen = !isOpen">
        {{ isOpen ? '收起' : '展开' }}
        <i
          :class="isOpen ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
          style="font-size: 16px;color: #0085FF"
        />
      </el-button>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      condition: {
        name: '',
        stage: '',
        time: ['', ''],
        stage_status: '',
        user_id: '',
        plan_name: ''
      },
      pickerOptions: {
        shortcuts: [
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit('pick', [start, end])
            }
          }
        ]
      },
      stageList: [
        {
          value: '',
          label: '全部'
        },
        {
          value: -1,
          label: '试岗阶段'
        },
        {
          value: 0,
          label: '第一阶段'
        },
        {
          value: 1,
          label: '第二阶段'
        },
        {
          value: '-2',
          label: '轮岗阶段'
        },
        {
          value: 2,
          label: '第三阶段'
        },
        {
          value: 3,
          label: '第四阶段'
        },
        {
          value: 4,
          label: '第五阶段'
        },
        {
          value: 5,
          label: '第六阶段'
        }
      ],
      stateList: [
        {
          value: '',
          label: '全部'
        },
        {
          value: 'doing',
          label: '正在进行'
        },
        {
          value: 'exam',
          label: '待考试'
        },
        {
          value: 'eliminate',
          label: '淘汰'
        },
        {
          value: 'delay',
          label: '延期'
        },
        {
          value: 'pass',
          label: '通过'
        }
      ],
      isOpen: false,
      stage: '',
      state: ''
    }
  },
  methods: {
    onSubmit() {
      this.$emit('searchList', this.condition)
    }
  }
}
</script>

<style lang="scss" scoped>
.search {
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
  margin-bottom: 20px;
  /deep/form {
    width: 100%;
    .el-date-editor {
      padding: 0 10px;
      width: 220px;
      height: 32px;
      .el-range-input {
        width: 85px;
        height: 30px;
      }
    }
  }
  .search-item {
    margin-right: 70px;
    color: #17233d;
    /deep/input {
      width: 220px;
      height: 32px;
    }
    /deep/span {
      top: -3px;
    }
    /deep/.el-input__suffix {
      top: 0;
    }
  }
  .select-btn {
    padding: 0 20px;
    width: 68px;
    height: 32px;
  }
  .arrow {
    float: right;
  }
}
</style>
